function parallaxInfrastructureInit() {


    let infrastructureAnimation_1 = document.querySelector(".infrastructure-animation_1");

    new simpleParallax(infrastructureAnimation_1, {
        orientation: 'down',
        overflow: true,
        delay: .9,
        scale: 1.6,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let infrastructureAnimation_2 = document.querySelector(".infrastructure-animation_2");

    new simpleParallax(infrastructureAnimation_2, {
        orientation: 'left',
        overflow: true,
        delay: .7,
        scale: 1.4,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let infrastructureAnimation_3 = document.querySelector(".infrastructure-animation_3");

    new simpleParallax(infrastructureAnimation_3, {
        orientation: 'down right',
        overflow: true,
        delay: .9,
        scale: 1.6,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_4 = document.querySelector(".infrastructure-animation_4");

    new simpleParallax(infrastructureAnimation_4, {
        orientation: 'down right',
        overflow: true,
        delay: 1.2,
        scale: 1.2,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_5 = document.querySelector(".infrastructure-animation_5");

    new simpleParallax(infrastructureAnimation_5, {
        orientation: 'down left',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_6 = document.querySelector(".infrastructure-animation_6");

    new simpleParallax(infrastructureAnimation_6, {
        orientation: 'top right',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_7 = document.querySelector(".infrastructure-animation_7");

    new simpleParallax(infrastructureAnimation_7, {
        orientation: 'down',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_8 = document.querySelector(".infrastructure-animation_8");

    new simpleParallax(infrastructureAnimation_8, {
        orientation: 'down',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let infrastructureAnimation_9 = document.querySelector(".infrastructure-animation_9");

    new simpleParallax(infrastructureAnimation_9, {
        orientation: 'down right',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_10 = document.querySelector(".infrastructure-animation_10");

    new simpleParallax(infrastructureAnimation_10, {
        orientation: 'down right',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let infrastructureAnimation_11 = document.querySelector(".infrastructure-animation_11");

    new simpleParallax(infrastructureAnimation_11, {
        orientation: 'down right',
        overflow: true,
        delay: 1.2,
        scale: 1.5,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });
 


}


//del animate class


let infrastructureAnimation_1 = document.querySelector(".infrastructure-animation_1");
if (infrastructureAnimation_1) {
    infrastructureAnimation_1.addEventListener('animationend', function () {
        infrastructureAnimation_1.classList.remove('animate__animated', 'animate__zoomInRight', 'animate__delay-2s')

    })
}


