



//custom 

function parallaxAboutInit() {


    let aboutAnimation_1 = document.querySelector(".about-animation_1");

    new simpleParallax(aboutAnimation_1, {
        orientation: 'down',
        overflow: true,
        delay: 1.2,
        scale: 1.4,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_2 = document.querySelector(".about-animation_2");

    new simpleParallax(aboutAnimation_2, {
        orientation: 'down left',
        overflow: true,
        delay: .7,
        scale: 1.4,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_3 = document.querySelector(".about-animation_3");

    new simpleParallax(aboutAnimation_3, {
        orientation: 'down right',
        overflow: true,
        delay: .9,
        scale: 1.6,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let aboutAnimation_4 = document.querySelector(".about-animation_4");

    new simpleParallax(aboutAnimation_4, {
        orientation: 'down right',
        overflow: true,
        delay: 1.2,
        scale: 1.9,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_5 = document.querySelector(".about-animation_5");

    new simpleParallax(aboutAnimation_5, {
        orientation: 'down right',
        overflow: true,
        delay: .9,
        scale: 1.9,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_6 = document.querySelector(".about-animation_6");

    new simpleParallax(aboutAnimation_6, {
        orientation: 'down',
        overflow: true,
        delay: 1.6,
        scale: 1.8,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    //circle

    let aboutAnimation_7 = document.querySelector(".about-animation_7");

    new simpleParallax(aboutAnimation_7, {
        orientation: 'down left',
        overflow: true,
        delay: 1.2,
        scale: 2,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_8 = document.querySelector(".about-animation_8");

    new simpleParallax(aboutAnimation_8, {
        orientation: 'up left',
        overflow: true,
        delay: 1.6,
        scale: 1.8,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_9 = document.querySelector(".about-animation_9");

    new simpleParallax(aboutAnimation_9, {
        orientation: 'down right',
        overflow: true,
        delay: 1.6,
        scale: 1.8,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });

    let aboutAnimation_10 = document.querySelector(".about-animation_10");

    new simpleParallax(aboutAnimation_10, {
        orientation: 'down left',
        overflow: true,
        delay: 1.6,
        scale: 1.8,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });


    let aboutAnimation_11 = document.querySelector(".about-animation_11");

    new simpleParallax(aboutAnimation_11, {
        orientation: 'up right',
        overflow: true,
        delay: 1.6,
        scale: 1.8,
        transition: 'cubic-bezier(0.25, 0.1, 0.25, 1.0)'
    });





}


//del animate class


let aboutAnimation_1 = document.querySelector(".about-animation_1");
if (aboutAnimation_1) {
    aboutAnimation_1.addEventListener('animationend', function () {
        aboutAnimation_1.classList.remove('animate__animated', 'animate__zoomInRight', 'animate__delay-2s')

        setTimeout(parallaxAboutInit, 1500);
    })
}


